<script src="../../../../../yijjixhaxun/vue/src/router/index.js"></script>
<template>
    <div id="stayls">
        <headerclass></headerclass>
        <pu-blicto></pu-blicto>
        <div class="Whole">
            <div class="claope">
                <div class="master">
                    <p class="chaing">统计查询</p>
                    <button class="Estaker" @click="clickExport">导出</button>
                    <button class="Establ" @click="information">查询</button>
                    <el-date-picker
                            v-model="value1"
                            type="daterange"
                            range-separator="至"
                            start-placeholder="开始日期"
                            end-placeholder="结束日期"
                            :clearable = "false"
                            :default-time="['00:00:00', '23:59:59']">
                    </el-date-picker>

                    <el-select v-model="value" placeholder="客户名称" class="mower">
                        <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.custName"
                                :value="item.custId">
                        </el-option>
                    </el-select>
                </div>
                <div class="">
                    <el-table
                            :data="tableData"
                            style="width: 100%">
                        <el-table-column
                                label="序号"
                                width="70"
                                type="index">
                        </el-table-column>
                        <el-table-column
                                prop="custName"
                                label="客户名称">
                        </el-table-column>
                        <el-table-column
                                prop="custNumber"
                                label="客户编号">
                        </el-table-column>
                        <el-table-column
                                prop="sceneName"
                                label="场景名称">
                        </el-table-column>
                        <el-table-column
                                prop="queryCount"
                                label="查询条数">
                        </el-table-column>
                    </el-table>
                </div>
                <div class="paging">
                    <div class="block_plo">
                        <el-pagination
                                @size-change="handleSizeChange"
                                @current-change="handleCurrentChange"
                                :current-page="currentPage"
                                :prev-text="'上一页'"
                                :next-text="'下一页'"
                                :page-count="pageCount"
                                :page-size="pagesize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="pageTotal">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import headerclass from "@views/Charging/header/header";
    import puBlicto from "@views/Charging/Administration/puBlicto";
    import {statQueryList,getAllCust,downQueryResult} from "@/network/admin";
    import {config} from "@/common/config";
    import  axios from 'axios';
    export default {
        name: "stayls",
        data() {
            return {
                tableData: [],
                selectedItem: {},
                loading: true,
                currentPage: 1,
                pagesize: 10,
                pageTotal: 0,
                pageCount: 0,
                //分页以上
                options: [],
                value: '',
                value1:'',
                mark:'',

            }
        },
        created: function () {
            this.hadleGetCustomerApi();
            this.Modulepake();
        },
        activated:function(){
            this.hadleGetCustomerApi();
            this.Modulepake();
        },
        components: {
            headerclass,
            puBlicto,
        },
        methods: {
            handleSizeChange: function (size) {
                this.pagesize = size;
                this.hadleGetCustomerApi();
                var itemsPerPage = this.pagesize;
            },
            // 控制页面的切换
            handleCurrentChange: function (currentPage) {
                this.currentPage = currentPage;
                var currentPage = this.currentPage;
                this.hadleGetCustomerApi();
            },
            //请求列表
            hadleGetCustomerApi() {
                var data = {
                    currentPage: this.currentPage,
                    itemsPerPage: this.pagesize
                };
                statQueryList(data).then(res => {
                    this.tableData = res.data.datas;
                    this.pageTotal = res.data.total;
                    this.pageCount = res.data.pages;
                })
                .catch({});
            },
            //获取所有客户列表
            Modulepake(){
                getAllCust().then(res => {
                    this.options = res.data;
                    this.options.unshift({
                        custId: '',
                        custName: "请选择客户名称"
                    })
                })
            },
            //查询信息
            information(){
                const custId = this.value;
                if (this.value1 == '') {
                    const data = {
                        custId: custId,
                        currentPage: this.currentPage,
                        itemsPerPage: this.pagesize,
                    };
                    statQueryList(data).then(res => {
                        this.tableData = res.data.datas;
                        this.pageTotal = res.data.total;
                        this.pageCount = res.data.pages;
                    })
                    return false;
                } else {
                    // --时间处理
                    const startDate = this.value1[0];
                    const endDate = this.value1[1];
                    let d = new Date(startDate);
                    d = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
                    let c = new Date(endDate);
                    c = c.getFullYear() + '-' + (c.getMonth() + 1) + '-' + c.getDate();
                    const data = {
                        custId: custId,
                        currentPage: this.currentPage,
                        itemsPerPage: this.pagesize,
                        startDate: d,
                        endDate: c
                    };
                    statQueryList(data).then(res => {
                        this.tableData = res.data.datas;
                        this.pageTotal = res.data.total;
                        this.pageCount = res.data.pages;
                    })
                    return false;
                }
            },
            //导出
            clickExport() {
                const custId = this.value;
                if (this.value1 == '') {
                    const data = {
                        custId: custId,
                        startDate:this.value1
                    };
                    downQueryResult(data).then(res => {
                        if (res.code) {
                            layer.msg(res.msg, {
                                icon: 2,
                                time: 1500
                            });
                            return false
                        }
                        let url = config().url + '/stat/downQueryResult';
                        Object.keys(data).forEach((key, index) => {
                            url += index === 0 ? '?' : '&';
                            url += `${key}=${encodeURIComponent(data[key])}`;
                        });
                        window.open(`${url}`, '_blank');
                    })
                } else {
                    // --时间处理
                    const startDate = this.value1[0];
                    const endDate = this.value1[1];
                    let d = new Date(startDate);
                    d = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate();
                    let c = new Date(endDate);
                    c = c.getFullYear() + '-' + (c.getMonth() + 1) + '-' + c.getDate();
                    const data = {
                        custId: custId,
                        startDate: d,
                        endDate: c
                    };
                    downQueryResult(data).then(res => {
                        if (res.code) {
                            layer.msg(res.msg, {
                                icon: 2,
                                time: 1500
                            });
                            return false
                        }
                        let url = config().url + '/stat/downQueryResult';
                        Object.keys(data).forEach((key, index) => {
                            url += index === 0 ? '?' : '&';
                            url += `${key}=${encodeURIComponent(data[key])}`;
                        });
                        window.open(`${url}`, '_blank');
                    })
                }
            },
        }
    }
</script>

<style>
    @import "~@assets/css/user/Addstatistics.css";
</style>